import React from 'react'

const Footer = ({
  styles
}) => (
  <footer style={styles} className="footer pt-5 pl-1 pr-1 bg-dark text-white">
    <div className="row">
      <div className="col-sm-4 mb-4 text-center text-sm-left">
        <div className="d-sm-flex justify-content-center align-items-center flex-column">
          <div>
            <p className="d-block lead">Austin Texas</p>
            <address>800 Brazos St Suite 500<br />Austin, TX 78741</address>  
          </div>
        </div>
      </div>
      <div className="col-sm-4 mb-4 text-center text-sm-center">
        <div className="d-sm-flex justify-content-center align-items-center flex-column">
          <div>
            <p className="d-block lead">General Inquiries</p>
            <span>contact@applynowleasing.com<br />1 (512) 956 1160</span>
          </div>
        </div>
      </div>
      <div className="col-sm-4 mb-4 text-center text-sm-right">
        <div className="d-sm-flex justify-content-center align-items-center flex-column">
          <div>
            <p className="d-block lead">Social Accounts</p>
            <p>Facebook<br />Twitter</p>  
          </div>
        </div>
      </div>
    </div>
    <div className="row text-center">
      <div className="col mb-3">
        <small> 2017 ApplyNowLeasing Inc. All rights reserved. Real estate technology provider.</small>
      </div>
    </div>
    <div className="row text-center">
      <div className="col mb-5">
        <small>About Us | Contact | SiteMap</small>
      </div>
    </div>
  </footer>
)

export default Footer



