import React, { Component } from 'react'
import { NavLink } from 'react-router-dom'
import Logo from '../Components/images/inlineLogo.png'

const AppBar2 = ({
  history,
  user = {},
}) => (
  <nav className="d-flex align-items-center nav nav-pills pl-2 pr-2 pb-3 border bg-white">
    <img
      alt=""
      className="d-none d-sm-inline-flex"
      style={{ marginRight: 20, height: 80 }}
      src={Logo}
    />
    <div className="d-flex justify-content-between f-1">
      <div className="d-flex flex-row pt-3">
        <NavLink
          exact
          className="nav-link"
          to="/"
          children="Home"
        />
        <NavLink
          exact
          className="nav-link"
          to="/contact"
          children="Contact"
        />
        <NavLink
          exact
          className="nav-link"
          to="/about"
          children="About Us"
        />
        <NavLink
          exact
          className="nav-link"
          to="/application1"
          children="View Example App"
        />
      </div>
      <div className="d-flex flex-row pt-3">
        {localStorage.getItem('auth') ? ([
          <NavLink
            key="1"
            className="nav-link mr-2"
            to="/dashboard"
            children="Dashboard"
            exact
          />,
          <NavLink
            key="2"
            className="nav-link bg-danger text-white"
            to="/login"
            onClick={() => localStorage.removeItem('auth')}
            children="Logout"
          />
        ]) : ([
          <NavLink
            key="1"
            className="nav-link mr-2"
            to="/login"
            children="Login"
            exact
          />,
          <NavLink
            key="2"
            className="nav-link"
            to="/signup"
            children="Signup"
            exact
          />
        ])}
      </div>
    </div>
  </nav>
)

export default AppBar2

